<template>
  <personal-info v-if="!showFakePass" @my-click="onClick" />
  <fake-pass v-if="showFakePass" @click-to-return="back" :personalInfo="personalInfo"/>
</template>

<script>
import FakePass from "@/components/FakePass";
import PersonalInfo from "@/components/PersonalInfo";
export default {
  components: { PersonalInfo, FakePass },
  data() {
    return {
      showFakePass: false,
      personalInfo: {
        name: "易玛㷩",
        stdId: 2020202020,
        dep: "计算机学院（国家示范性软件学院）",
        inOut: "允许出校",
        img: require("@/img/default.jpg"),
      },
    };
  },
  mounted() {
    document.body.addEventListener("touchstart", () => {});
  },
  methods: {
    onClick(pi) {
      this.personalInfo = pi;
      this.showFakePass = true;
    },
    back() {
      this.showFakePass = false;
    },
  },
};
</script>

